* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  background-color: violet;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.loader {
  color: #f7f4f4;
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  background-color: currentColor;
  border-radius: 50%;
  font-size: 48px;
  position: relative;
  transform: rotateX(-60deg)perspective(1000px);
}

.loader:before, .loader:after {
  content: "";
  box-sizing: border-box;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  justify-content: center;
  animation: flowerFlow 1s ease-out infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  animation-delay: .4s;
}

@keyframes flowerFlow {
  0% {
    opacity: 1;
    transform: rotate(0deg);
    box-shadow: 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em;
  }

  100% {
    opacity: 0;
    transform: rotate(180deg);
    box-shadow: -1em -1em 0 -.35em, 0 -1.5em 0 -.35em, 1em -1em 0 -.35em, -1.5em 0 0 -.35em, 1.5em 0 0 -.35em, -1em 1em 0 -.35em, 0 1.5em 0 -.35em, 1em 1em 0 -.35em;
  }
}

.breed-select {
  color: #202022;
  background-color: #a0b7e2;
  border: 2px solid #464da8;
  border-radius: 5px;
  padding: 5px;
  font-size: 18px;
}

.breed-select:focus {
  outline: 2px solid #464da8;
}

.cat-info {
  flex-direction: column;
  margin-top: 20px;
  display: flex;
}

.cat-img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  box-shadow: 1px 1px 18px #464da8;
}

.cat-text {
  border: 2px solid #464da8;
  border-radius: 20px;
  margin-top: 20px;
  padding: 20px;
}

.cat-header:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #464da8;
  display: block;
  box-shadow: 1px 1px 18px #464da8;
}

.hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 428px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }

  .cat-img {
    min-height: 100%;
  }

  .cat-info {
    flex-direction: row;
    align-items: center;
  }

  .cat-img {
    width: 400px;
  }

  .cat-text {
    border: none;
    flex-grow: 1;
    margin-top: 0;
    margin-left: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }

  .cat-info {
    flex-direction: row;
    align-items: normal;
  }

  .cat-img {
    width: 600px;
  }

  .cat-text {
    border: 2px solid #464da8;
  }
}

